export const mixins = {
    data() {
        return {
          slideDown: false,
          slideOutUp: false
        }
      },
    methods: {
        handleWheel(event) {
            const deltaY = event.deltaY;
            const scrollContainer = this.$refs.scrollContainer;
            // 更新滚动位置
            const top = scrollContainer.scrollTop + deltaY;
            // 阻止默认滚动行为，以便我们手动处理滚动
            // event.preventDefault();
            console.log("event", scrollContainer.scrollTop+deltaY);
            const scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
            console.log("1111", scrollTop);
            if (deltaY > 0) {
              this.slideOutUp=true
              this.slideDown = false;
              console.log('向下滚动');
              if(scrollTop >= 100){
                // this.showText()
                this.fnGetTextList()
                if(scrollTop >= 859){
                    this.fnshow()
                }else{
                    this.fnClearShow()
                }
              }else{
                this.fnClearText()
              }
            } else if (deltaY < 0) {
              if( scrollTop === 0 ){
                return
              }
              this.slideDown=true
              this.slideOutUp = false;
              // console.log('向上滚动');
            }
          }
    },
  };
  