                </span>
              </p>
            </div>
            <img class="wallet animate__animated" :class="{'animate__fadeIn': animate__slideInUp, 'hidden': !animate__slideInUp}" src="@/assets/wallet.png" alt="" :style="{ 'animation-duration': '5000ms'}">
          </div>
          <div class="desc animate__animated" :class="{'animate__slideInUp': animate__slideInUp,'hidden': !animate__slideInUp}">
            <p v-for="(list, index) in lists" :key="index">
              <span class="title animate__animated animate__slideInUp" :style="{ 'animation-duration': '2000ms'}">
                {{ list.title }}
              </span>
              <span class="text">
                {{ list.text }}
                </span>
            </p>
