<script>
// @ is an alias to /src
import { mixins } from "@/mixins/mixins";
import Footer from '@/components/Footer.vue'
export default {
  mixins: [mixins],
  data() {
    return {
      letters: 'NUESTRA'.split(''),
      competitividad: 'COMPETITIVIDAD'.split(''),
      Préstamo: 'Guía_de_Préstamo'.split(''),
      displayDelay: 0,
      displayedLetters: [],
      items: [],
      titles: [],
      animate__slideInUp: false,
      lists:[
        {
          title: 'Mayor monto, menor interés',
          text: 'Puedes confiar en que nuestros productos ofrecen bajos intereses y que cumplimos con todas las normativas para proporcionarte la mejor opción disponible.'
        },
        {
          title: 'Préstamo Fácil en Minutos',
          text: 'Obtén tu préstamo de manera rápida y sencilla. Con un proceso simplificado, puedes acceder al financiamiento que necesitas en solo minutos, sin complicaciones ni demoras.'
        },
        {
          title: 'Variedad de Opciones',
          text: 'Ofrecemos flexibilidad para que ajustes tu ritmo de pago. Tienes la libertad de seleccionar el monto del préstamo, el plazo y la cantidad de pagos según tus necesidades.'
        }
      ],
      elements: [
        {
          name:1,
          img: require('@/assets/completa.png'),
          title: 'Completa el Formulario:',
          text: 'Ingresa tus datos personales y financieros en nuestro formulario en línea.'
        },
        {
          name:2,
          img: require('@/assets/completa.png'),
          title: 'Revisión de Documentos:',
          text: 'Sube los documentos necesarios para verificar tu identidad y situación financiera.'
        },
        {
          name:3,
          img: require('@/assets/completa.png'),
          title: 'Recepción del Préstamo:',
          text: 'Una vez aprobados, recibe los fondos directamente en tu cuenta bancaria.'
        },
      ],
      activeIndex: 0,
      timerId: null,
    }
  },
  components: {
    Nav: () => import('@/components/NavBar.vue'),
    Footer: () => import('@/components/Footer.vue')
  },
  computed: {

  },
  mounted() {
    this.startInterval()
    window.addEventListener('beforeunload', this.handleBeforeUnload)
  },
  beforeDestroy() {
    this.stopInterval()
    window.removeEventListener('beforeunload', this.handleBeforeUnload)
  },
  methods: {
    handleBeforeUnload(event) {
      // 执行页面刷新前的操作
      console.log('页面即将刷新或关闭');
      window.scrollTo(0, 0)
    }, 
    startInterval() {
      // 确保在开始新的定时器之前清除旧的定时器
      if (this.timerId) {
        clearInterval(this.timerId);
      }
      this.timerId = setInterval(() => {
        if (this.activeIndex >= 2) {
          this.activeIndex = 0;
        } else {
          this.activeIndex++;
        }
      }, 5000);
    },
    restartInterval() {
      // 重置value并重新开始定时器
      this.activeIndex = 0;
      this.startInterval();
    },
    stopInterval() {
      if (this.timerId) {
        clearInterval(this.timerId);
        this.timerId = null;
      }
    },
    zoomElement(val) {
      this.activeIndex = val
    },
    fnClearText(){
      this.displayedLetters = []
      this.items = []
      this.animate__slideInUp = false
    },
    fnGetTextList(){
      this.displayedLetters = this.showText(this.letters)
      this.items = this.showText(this.competitividad)
      this.animate__slideInUp = true
    },
    fnshow(){
      this.titles = this.showText(this.Préstamo)
    },
    fnClearShow(){
      this.titles =[]
    },
    showText(text) {
      return text.map((letter, index) => {
        return {
          letter: letter,
          display: index < text.length, // 控制字母是否显示
          animationDelay: `${index * 0.05}s` // 每个字母的显示延迟
        };
      });
    }
  },
}
</script>
